@import url(https://fonts.googleapis.com/css2?family=Ubuntu+Mono:ital,wght@0,400;0,700;1,400;1,700&display=swap);
.container-custom {
	overflow-y: scroll;
	height: 64vh;
}
.container::-webkit-scrollbar {
	width: 5px; /* width of the entire scrollbar */
}
.container::-webkit-scrollbar-track {
	/* background: rgb(255, 255, 255);  */
	background: #fd746c; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #fd746c, #ff9068); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.container::-webkit-scrollbar-thumb {
	background-color: rgba(54, 54, 54, 0.466); /* color of the scroll thumb */
	border-radius: 10px; /* roundness of the scroll thumb */
	border: 3px solid rgba(78, 78, 78, 0.452); /* creates padding around scroll thumb */
}
.inprogress-card {
	background-color: #d40707;
	border-radius: 15px;
	height: 90%;
}
.topicName {
	color: #333333;
	font-size: 25px;
	font-weight: 700;
}
.totalQuestion {
	color: #4f4f4f;
	font-weight: 700;
}
.notstarted-card {
	background-color: #e5e90b;
	border-radius: 15px;
	height: 90%;
}

.percentDone {
	color: #4f4f4f;
}

.container-custom2 {
	width: 40vw;
	min-width: 290px !important;
}

.hvr-grow {
	vertical-align: middle;
	transform: perspective(0.5px) translateZ(0);
	box-shadow: 0 0 1px rgba(0, 0, 0, 0);
	transition-duration: 0.3s;
	transition-property: transform;
}
.hvr-grow:hover,
.hvr-grow:focus,
.hvr-grow:active {
	transform: scale(1.05);
}

@media screen and (max-height: 650px) {
	.container-custom {
		height: 56vh;
	}
}

@media screen and (max-height: 750px) {
	.container-custom {
		height: 54vh;
	}
}

.Toastify__toast-body {
	color: rgb(38, 140, 173);
	font-family: "Ubuntu Mono", monospace;
	font-weight: 600;
	padding: 0 0.5rem;
}

.Toastify__toast-body .toast-subtitle {
	font-weight: 400;
	font-size: small;
}

/* Marked Complete */
.Toastify__toast.toast-Done {
	background: #27ae60;
}

.Toastify__toast.toast-Done .Toastify__progress-bar {
	background: #c8e6c9;
}

/* Marked Incomplete */
.Toastify__toast.toast-Incomplete {
	background: #007bff;
}

.Toastify__toast.toast-Incomplete .Toastify__progress-bar {
	background: #aad0f6;
}

header {
  position: relative;
  height: 800px;
  min-height: 500px;
  width: 100%;
  
  text-align: center;
  overflow: hidden;
}

/* vertically center banner section */
header:before {
  content: '';
  display: inline-block;
  vertical-align: middle;
  height: 100%;
}
header .banner {
  display: inline-block;
  vertical-align: middle;
  margin: 0 auto;
  width: 85%;
  padding-bottom: 30px;
  text-align: center;
}

header .banner-text { width: 100%; }
header .banner-text h1 {
  font: 90px/1.1em 'opensans-bold', sans-serif;
  color: #fff;
  letter-spacing: -2px;
  margin: 0 auto 18px auto;
}
header .banner-text h3 {
  font: 18px/1.9em 'librebaskerville-regular', serif;
  color: #ddd;
  margin: 0 auto;
  width: 70%;
}
header .banner-text h3 span,
header .banner-text h3 a {
  color: #fff;
}
header .banner-text hr {
  width: 60%;
  margin: 18px auto 24px auto;
  border-color: #2F2D2E;
  border-color: rgba(150, 150, 150, .1);
}

/* header social links */
header .social {
  margin: 24px 0;
  padding: 0;
  font-size: 30px;
}
header .social li {
  display: inline-block;
  margin: 0 15px;
  padding: 0;
}
header .social li a { color: #fff; }
header .social li a:hover { color: #0762f9; }

/* scrolldown link */
header .scrolldown a {
  position: absolute;
  bottom: 30px;
  left: 50%;
  margin-left: -29px;
  color: #fff;
  display: block;
  height: 42px;
  width: 42px;
  font-size: 42px;
  line-height: 42px;
  color: #fff;
  border-radius: 100%;
  transition: all .3s ease-in-out;
}
header .scrolldown a:hover { color: #0762f9; }

.footer {
	position: absolute;
	bottom: 0;
	width: 100%;
	height: 45px; 
	background-color: #27ae60;
}

.app-heading {
	font-weight: 700;
	color: #343a40;
	font-size: 50px;
	background-color: #ff9068;
}

@media screen and (max-width: 480px) {
	.app-heading {
		font-size: 30px;
	}
	.app-heading2 {
		font-size: 20px;
	}
}

body {
  margin: 0;
  overflow-y: hidden;
  font-family: 'Ubuntu Mono', monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
